#app3 {
    border: 1px solid yellow;
    width: 50vw;
    height: 50vh;
}

#app3 .square {
    width: 10vw;
    height: 10vw;
    margin-top: 5vw;
    margin-left: 5vw;
    border: 1px solid orange;
    transition: transform 1s;
}

#app3 .square.active {
    transform: translateX(10vw);
}